<template>
	<div class="content">
		<div class="title">
			<span>其他组合/乐队</span>
		</div>
		<div class="pic">
			<div class="pic1" v-for="item in singer">
				<img title="歌手详情" :src="item.picUrl" @click="otherband(item.id)"><img>
				<p>{{item.name}}</p>
			</div>
		</div>
	</div>
</template>

<script>
import axios from 'axios'
export default {
	data () {
			return {
			  singer:[],//存放歌手信息
			};
	},
	created(){
			  this.getSinger();//获取歌手信息
	},
	methods:{
			  getSinger(){
				  var that=this;
				  axios.get("https://autumnfish.cn/artist/list?type=3&area=0&limit=25").then((res)=>{
					  that.singer=res.data.artists;
				  },(err)=>{})
			  },
			  otherband(id){
				  this.$store.commit("singerId",id);
				  this.$router.push({
					  name:"singerdets",
				  })
			  }
	}
}
</script>

<style scoped>
	.content{
		position: absolute;
		width: 70%;
		right: 80px;
		top: 20px;	
		height: 1000px;
	}
	.title{
		width: 950px;
		border-bottom: 2px solid #c20c0c;
		height: 30px;
	}
	.title span{
		font-size: 20px;
		font-weight: normal;
	}
	.pic{		
		margin-top: 20px;
		height: 320px;
		width: 1000px;
	}
	.pic1{
		padding: none;
		margin: 0 auto;
		float:left;
		width: 195px;
		height: 195px;
	}
	.pic1 img{
		box-shadow:3px 3px 3px 3px rgba(151, 147, 148, 0.5);
		cursor:pointer;
		margin: 0 auto;		
		height: 70%;
	}

</style>
